/* eslint-disable react-hooks/exhaustive-deps */
// @mui
import { Container, Grid, Typography, Card, Box, Stack } from '@mui/material';
import { LoadingButton } from '@mui/lab';
import { useState, useEffect } from 'react';
import { useSnackbar } from 'notistack';
// hooks
import useSettings from '../../hooks/useSettings';
// components
import Page from '../../components/Page';
import HeaderBreadcrumbs from '../../components/HeaderBreadcrumbs';
import axios from '../../utils/axios';

// sections
import {
  Appbalance,
  NumberForm
} from '../../sections/@dashboard/general/app';
import UserChat from '../../sections/@dashboard/admin/userchat';
// ----------------------------------------------------------------------

export default function AdminGeneralApp() {
  const { themeStretch } = useSettings();
  const [myusers, RealUser] = useState([]);
  const [load, SetLoad] = useState(true);
  const [lazy, SetLazy] = useState(false);
  const { enqueueSnackbar } = useSnackbar();
  const AccessToken = window.localStorage.getItem('accessToken');

  useEffect(() => {
    initialize();
  }, []);

  const initialize = async () => {
    SetLoad(true)
    await axios.get(`/api/system/all/user/records/admin/safe/url/${AccessToken}/secure`).then(data => {
      RealUser(data.data.user);
      SetLoad(false)
    }).catch(() => SetLoad(false));
  }

  const MakeSuccess = async () => {
    SetLazy(true);
    try {
      await axios.get('/api/success/system/success');
      SetLazy(false)
      initialize();
      enqueueSnackbar('success')
    } catch (_) {
      SetLazy(false)
      enqueueSnackbar('error', { variant: 'error' })
    }
  }

  const MakeFail = async () => {
    SetLazy(true);
    try {
      await axios.get('/api/refund/system/refund');
      initialize();
      SetLazy(false)
      enqueueSnackbar('success')
    } catch (_) {
      SetLazy(false)
      enqueueSnackbar('error', { variant: 'error' })
    }
  }
  return (
    <Page title="Admin Dashboard">

      <Container maxWidth={themeStretch ? false : 'xl'}>
        <HeaderBreadcrumbs
          heading={'Admin Dashboard Developed By A.B.O.K.I developers'}
          links={[
            { name: 'support' },
            { name: '07040540018' },
          ]}
        />

        <Grid container spacing={3}>

          {!load ? <> <Grid item xs={12} md={4}>
            <Appbalance
              title="Total Users Wallet Balance"
              total={myusers?.wallet_balance}
            />
          </Grid>
            <Grid item xs={12} md={4}>
              <Appbalance
                title="Total User's Earning Balance"
                total={myusers?.ref_balance}
              />
            </Grid>
            <Grid item xs={12} md={4}>
              <NumberForm
                title="Total User"
                total={myusers?.all_user}
              />
            </Grid>
            <Grid item xs={12} md={4}>
              <NumberForm
                title="Total Smart User"
                total={myusers?.smart_total}
              />
            </Grid>

            <Grid item xs={12} md={4}>
              <NumberForm
                title="Total Awuf User"
                total={myusers?.awuf_total}
              />
            </Grid>
            <Grid item xs={12} md={4}>
              <NumberForm
                title="Total AGENT User"
                total={myusers?.agent_total}
              />
            </Grid>
            <Grid item xs={12} md={4}>
              <NumberForm
                title="Total API User"
                total={myusers?.api_total}
              />
            </Grid>
            <Grid item xs={12} md={4}>
              <NumberForm
                title="Total Special User"
                total={myusers?.special_total}
              />
            </Grid>

            <Grid item xs={12} md={4}>
              <NumberForm
                title="Total CUSTOMER CARE"
                total={myusers?.customer_total}
              />
            </Grid>
            <Grid item xs={12} md={4}>
              <NumberForm
                title="Total Admin"
                total={myusers?.admin_total}
              />
            </Grid>
            <Grid item xs={12} md={4}>
              <Appbalance
                title="Total MTN CG Balance"
                total={myusers?.mtn_cg_bal}
              />
            </Grid>
            <Grid item xs={12} md={4}>
              <Appbalance
                title="Total MTN GIFTING Balance"
                total={myusers?.mtn_g_bal}
              />
            </Grid>
            <Grid item xs={12} md={4}>
              <Appbalance
                title="Total MTN SME Balance"
                total={myusers?.mtn_sme_bal}
              />
            </Grid>
            <Grid item xs={12} md={4}>
              <Appbalance
                title="Total AIRTEL CG Balance"
                total={myusers?.airtel_cg_bal}
              />
            </Grid>
            <Grid item xs={12} md={4}>
              <Appbalance
                title="Total AIRTEL GIFTING Balance"
                total={myusers?.airtel_g_bal}
              />
            </Grid>
            <Grid item xs={12} md={4}>
              <Appbalance
                title="Total AIRTEL SME Balance"
                total={myusers?.airtel_sme_bal}
              />
            </Grid>
            <Grid item xs={12} md={4}>
              <Appbalance
                title="Total GLO CG Balance"
                total={myusers?.glo_cg_bal}
              />
            </Grid>
            <Grid item xs={12} md={4}>
              <Appbalance
                title="Total GLO GIFTING Balance"
                total={myusers?.glo_g_bal}
              />
            </Grid>
            <Grid item xs={12} md={4}>
              <Appbalance
                title="Total GLO SME Balance"
                total={myusers?.glo_sme_bal}
              />
            </Grid>
            <Grid item xs={12} md={4}>
              <Appbalance
                title="Total 9MOBILE CG Balance"
                total={myusers?.mobile_cg_bal}
              />
            </Grid>
            <Grid item xs={12} md={4}>
              <Appbalance
                title="Total 9MOBILE GIFTING Balance"
                total={myusers?.mobile_g_bal}
              />
            </Grid>
            <Grid item xs={12} md={4}>
              <Appbalance
                title="Total 9MOBILE SME Balance"
                total={myusers?.mobile_sme_bal}
              />
            </Grid>
            <Grid item xs={12} md={4}>
              <Card sx={{ display: 'flex', alignItems: 'center', p: 3 }}>
                <Box sx={{ flexGrow: 1 }}>
                  <Typography variant="subtitle2">Total Processing Transaction</Typography>
                  <Typography variant="h3">{myusers?.total_process}</Typography>
                </Box>

              </Card>
            </Grid>
            <Grid item xs={12} md={4}>
              <Card sx={{ p: 3 }}>
                <Box sx={{ flexGrow: 1 }}>
                  <Typography variant="subtitle2">Total Data Processing Transaction</Typography>
                  <Typography variant="h3">{myusers?.total_data_proccess}</Typography>
                </Box>
                <Stack direction="row" spacing={1.5}>
                  <LoadingButton fullWidth size="large" type="submit" variant="contained" loading={lazy} onClick={() => MakeSuccess()}>
                    Make All Success
                  </LoadingButton>
                  <LoadingButton fullWidth size="large" color="error" type="submit" variant="contained" loading={lazy} onClick={() => MakeFail()}>
                    Refund All
                  </LoadingButton>
                </Stack>
              </Card>
            </Grid>
            <Grid item xs={12} md={4}>
              <UserChat myusers={myusers} />
            </Grid></> : <Typography variant="variable" >Loading ...</Typography>}
        </Grid>

      </Container>
    </Page>
  );
}
